/*-- Common Style --*/
body {
  line-height: 24px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  visibility: visible;
  font-family: "Poppins", sans-serif;
  color: #858585;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #525252;
  font-weight: bold;
  margin-top: 0;
}

p:last-child {
  margin-bottom: 0;
}

a,
button {
  color: inherit;
  display: inline-block;
  line-height: inherit;
  text-decoration: none;
}

a,
i,
button,
img,
input,
span,
*:before,
*:after {
  -webkit-transition: all 0.3s ease 0s;
  -ms-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

*:focus {
  outline: 0px solid;
}

a:focus {
  color: inherit;
  outline: medium none;
  transform: scale(1);
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul {
  list-style: outside none none;
  margin: 0;
  padding: 0;
}

/*-- Common Class --*/
.fix {
  overflow: hidden;
}

.clear {
  clear: both;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.section {
  float: left;
  width: 100%;
}

.section.bg-gray {
  background-color: #f1f1f1;
}

.section.bg-white {
  background-color: #fff;
}

