/*-- Scroll Up --*/
#scrollUp {
  width: 40px;
  height: 40px;
  background-color: #b42251;
  color: #fff;
  right: 40px;
  bottom: 40px;
  text-align: center;
  overflow: hidden;
}

@media only screen and (max-width: 479px) {
  #scrollUp {
    display: none !important;
  }
}

#scrollUp i {
  display: block;
  line-height: 40px;
  font-size: 24px;
}

#scrollUp:hover i {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

/*-- Section Title --*/
.section-title h1 {
  font-size: 36px;
  color: #525252;
  text-transform: uppercase;
  line-height: 28px;
  padding-bottom: 29px;
  margin: 0;
  position: relative;
  font-family: Montserrat;
}

@media only screen and (max-width: 767px) {
  .section-title h1 {
    font-size: 30px;
    line-height: 22px;
  }
}

@media only screen and (max-width: 479px) {
  .section-title h1 {
    font-size: 20px;
    line-height: 14px;
    padding-bottom: 15px;
  }
}

.section-title h1::before {
  border-bottom: 2px dashed #525252;
  bottom: 0;
  content: "";
  height: 2px;
  left: 50%;
  margin-left: -51px;
  position: absolute;
  width: 102px;
}

.section-title.title-white h1 {
  color: #fff;
}

.section-title.title-white h1::before {
  border-color: #fff;
}

