/*  2.  Header Area
/*----------------------------------------*/
/*-- Header Top Bar --*/
.header-top-bar {
  width: 100%;
  display: block;
  float: left;
}

.header-top-bar span {
  width: 25%;
  display: block;
  float: left;
  height: 8px;
}

.header-top-bar span.blue {
  background-color: #00d8ff;
}

.header-top-bar span.green {
  background-color: #06ca75;
}

.header-top-bar span.navy {
  background-color: #eea11d;
}

.header-top-bar span.red {
  background-color: #b42251;
}

/*-- Header Area --*/
.header-area {
  float: left;
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 8px;
  background-color: #001854;
  z-index: 99999;
}

.header-area.stick {
  position: fixed;
  top: 0;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
  -webkit-animation: 300ms ease-in-out 0s normal none 1 running fadeInDown;
  animation: 300ms ease-in-out 0s normal none 1 running fadeInDown;
}

.header-area.stick .logo {
  display: block;
  margin: 10px 0;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area.stick .logo {
    margin: 26px 0 25px;
  }
}

@media only screen and (max-width: 767px) {
  .header-area.stick .logo {
    margin: 16px 0 15px;
  }
}

.header-area.stick .header-search {
  padding: 26px 0;
}

@media only screen and (max-width: 767px) {
  .header-area.stick .header-search {
    padding: 16px 0;
  }
}

.header-area.stick .menu-toggle {
  margin: 26px 0 26px 29px;
}

@media only screen and (max-width: 767px) {
  .header-area.stick .menu-toggle {
    margin: 16px 0 16px 15px;
  }
}

.header-area.stick .main-menu {
  margin: 26px 0;
}

@media only screen and (max-width: 767px) {
  .header-area.stick .main-menu {
    margin: 0;
  }
}

.header-area .logo {
  display: block;
  margin: 10px 0;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area .logo {
    margin: 36px 0 35px;
    width: 140px;
  }
}

@media only screen and (max-width: 767px) {
  .header-area .logo {
    margin: 26px 0 25px;
    width: 140px;
  }
}

/* .header-area .logo img {
  width: 100%;
} */

/*-- Animation For Stick Menu --*/
@-webkit-keyframes fadeInDown {
  from {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-transform: none;
    transform: none;
  }
}

/*-- Menu Toggle --*/
.header-buttons {
  margin-left: 29px;
  position: relative;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-buttons {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .header-buttons {
    margin-left: 15px;
  }
}

/*-- Header Search --*/
.header-search {
  padding: 36px 0;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .header-search {
    padding: 26px 0;
    position: static;
  }
}

.header-search .search-toggle {
  display: block;
  width: 28px;
  height: 28px;
  background-color: #b42251;
  border-radius: 50px;
  color: #fff;
  border: none;
}

.header-search .search-toggle:hover {
  background-color: #eb184a;
}

.header-search .search-toggle i {
  font-size: 18px;
  line-height: 28px;
}

.header-search-form {
  position: absolute;
  right: 0;
  top: 100%;
  z-index: 999;
  background-color: #fff;
  padding: 15px;
  border: 1px solid #f1f1f1;
  margin-top: 30px;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s ease 0s;
  -ms-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.header-search-form.active {
  margin-top: 0;
  visibility: visible;
  opacity: 1;
}

.header-search-form form {
  width: 256px;
  margin: 0;
}

.header-search-form form input {
  display: block;
  float: left;
  width: 220px;
  border: 1px solid #f1f1f1;
  padding: 5px 10px;
  font-weight: 300;
  height: 36px;
}

.header-search-form form .submit {
  height: 36px;
  border: none;
  background-color: #f1f1f1;
  width: 36px;
  color: #525252;
}

.header-search-form form .submit:hover {
  background-color: #b42251;
  color: #fff;
}

.header-search-form form .submit i {
  font-size: 18px;
  line-height: 36px;
  display: block;
}

.menu-toggle {
  margin: 36px 0 36px 29px;
  width: 28px;
  height: 28px;
  border: none;
  background-color: #b42251;
  border-radius: 50px;
  color: #fff;
  padding: 0;
  overflow: hidden;
}

.menu-toggle:hover {
  background-color: #eb184a;
}

@media only screen and (max-width: 767px) {
  .menu-toggle {
    margin: 26px 0 26px 15px;
  }
}

.menu-toggle i {
  display: block;
  font-size: 16px;
  line-height: 28px;
}

/*-- Menu --*/
.main-menu {
  margin: 36px 0;
}

@media only screen and (max-width: 767px) {
  .main-menu {
    margin: 0;
    width: 100%;
    float: left;
  }
}

@media only screen and (max-width: 767px) {
  .main-menu nav {
    padding-bottom: 10px;
  }
}

.main-menu nav ul li {
  display: block;
  float: left;
  margin-left: 40px;
}

@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .main-menu nav ul li {
    margin-left: 35px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .main-menu nav ul li {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .main-menu nav ul li {
    float: none;
    margin-left: 0;
    margin-bottom: 10px;
  }
}

.main-menu nav ul li:first-child {
  margin-left: 0;
}

.main-menu nav ul li a {
  border-top: 1px solid transparent;
  display: block;
  text-transform: uppercase;
  font-size: 14px;
  color: #fff;
  line-height: 22px;
  font-weight: 500;
  margin: 3px 0 2px;
